<template>
	<div>
		<navigation-drawer ref="navigationDrawer" />
		<topo 
			:title="title"
			:backButton="backButton"
			:backButtonRouteName="backButtonRouteName" 
			:menuButton="menuButton"
			@navigationDrawerClicked="toggleNavigationDrawer()"
		/>
		<main class="site-main">
			<v-container :class="{ 'container--fluid': fluid, 'pa-0': noPadding }">
				<div v-show="!loading">
					<slot></slot>
					<v-btn
						v-if="fab"
						class="site-main-fab"
						:class="{fabText: 'fab-text'}"
						color="secondary"
						dark
						fixed
						bottom
						right
						fab
						@click="fabClicked"
					>
						<span v-if="fabText">
							{{ fabText }}	
						</span>
						<v-icon>{{ fabIcon }}</v-icon>
					</v-btn>
				</div>
			</v-container>
			<v-progress-circular
				v-show="loading"
				indeterminate
				size="50"
				color="secondaryDark"
			></v-progress-circular>
		</main>
		<rodape
      class="primaryDark"
      :items="footerItems" 
      :bottomNav="alias"
    />
	</div>
</template>

<script>
// Components
import NavigationDrawer from '@/components/site/navigation-drawer/NavigationDrawer'
import Rodape from '@/components/globais/footer/Footer'
import Topo from '@/components/site/template/Header'

export default {
	name: 'Footer',
	components: { NavigationDrawer, Rodape, Topo },
	props: {
		alias: { default: '' },
		backButton: { default: false },
		backButtonRouteName: { default: null },
		fab: { default: null },
		fabIcon: { default: 'mdi-plus' },
		fabText: { default: null },
		fluid: { default: false },
		loading: { default: false },
		menuButton: { default: false },
		noPadding: { default: false },
		title: { default: 'Meu Barbeiro' }
	},
	data: () => ({
		footerItems: [
      { title: 'Barbearias', icon: 'home', value: 'dashboard', routeName: 'home' },
      { title: 'Agendamentos', icon: 'calendar_today', value: 'schedule', routeName: 'site-schedule' }
    ]
	}),
	methods: {
		fabClicked () {
			this.$emit('fabClicked')
		},
		toggleNavigationDrawer () {
			this.$refs.navigationDrawer.toggleNavigationDrawer()
		}
	}
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.site-main { min-height: 100vh; padding-bottom: 60px; padding-top: 50px; background-color: $primary;
  .v-progress-circular { margin: auto; position: absolute; top: 0; bottom: 0; left: 0; right: 0; }
  .site-main-fab {
    color: $primary !important;
    z-index: 5;
    bottom: 70px;
    @include border-radius-mixin (45px);
		@include transition-mixin (all, .5s);
		
    > span span { display: none; }

    &:hover {
      width: fit-content;
      padding: 0 15px;
      @include border-radius-mixin (45px);
      @include transition-mixin (all, .5s);
    
      > span span { display: inline; }
    }
  }
}
</style>